-align:center;position:absolute;left:10px;top:10px;color:black;padding:5px;font-size:20px}.aboutUs__description{display:grid;width:60%;grid-template-columns:30% 1fr 1fr 1fr;grid-template-rows:180px 1fr;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;padding:15px}.aboutUs__description__header{grid-column-start:2;grid-column-end:5;text-align:left;margin-top:0;padding-top:100px}.aboutUs__description__text{margin-top:0;font-size:15px;line-height:20px}.aboutUs__foto{background-image:url(/hairdresser-app/static/media/about-us-decoration.e272fdb9.png);background-position:center;background-size:cover;width:35%;height:300px;z-index:1000;position:absolute;left:30%;top:300px;color:black;padding:5px;transform:translate(-50%, -50%);border-radius:25px}.aboutUs__logo{width:34%;background-color:#3797ce}.userPanel{width:100vw;min-height:100vh;background-color:black;position:relative;display:flex;color:white}.userPanel__header{font-size:80px;font-family:"Dancing Script", cursive;color:white;margin-top:80px;position:absolute;left:50%;top:20px;transform:translate(-50%, -50%)}.userPanel__info{width:50%;height:100%;display:flex;flex-direction:column;justify-content:center;align-items:flex-start;line-height:50px;margin-top:140px}.userPanel__info__button{background:none;margin:20px 10px;background-color:yellow;padding:10px;border-radius:5px}.userPanel__info--visits{display:flex;align-items:center;flex-direction:column;text-align:center;border:2px solid yellow;width:80%;height:50%;margin:50px 50px 50px 25px;border-radius:15px}.userPanel__info__data{display:flex;flex-direction:column;justify-content:center;align-items:center;border:2px solid yellow;width:80%;height:50%;margin:50px 25px 50px 50px;border-radius:15px}.visit{display:flex}.visit__button{margin:5% 10px;height:50%;align-items:center;justify-content:center2}*{margin:0}a{text-decoration:none;padding:5px;border-radius:10px;color:black}a:hover{box-sizing:border-box;border:2px solid #3d3d38}

